html, body {
    font-family: 'Open Sans', Helvetica, sans-serif;
}

.text-wrap {
    word-wrap: break-word;
}

.btn-group-xs > .btn, .btn-xs {
    padding: .25rem .4rem;
    font-size: .875rem;
    line-height: .5;
    border-radius: .2rem;
}

.img {
    width: 160px;
    height: 160px;
    margin: 0 auto;
    border-radius: 50%;
    overflow: hidden;
}

.md-link:hover {
    color: #007bff;
}